<template>
    <div class='demand_look'>
        <div class="header">
            <div class="back"  @click.stop="back()">
                <img src="../../assets/image/联合42.png" alt="">
            </div>
            <div class="demandHead">需求提报</div>
        </div>
        <div class='demandContainer'>
            <div class='demandName'>
                <div class='name'>名称</div>
                <el-input
                    placeholder="请输入"
                    v-model="nameinput"
                    maxlength="30"
                    disabled
                    clearable>
                </el-input>
            </div>
            <div class='demandType'>
                <div class='type'>类型</div>
                <el-select v-model="selectType" placeholder="请选择" disabled>
                    <el-option
                        v-for="item in type"
                        :key="item.code"
                        :value="item.name">
                    </el-option>
                </el-select>
            </div>
            <div class='middle'></div>
        </div>
        <div class='demandContain'>
            <el-input
                disabled
                maxlength="500"
                type="textarea"
                autosize
                placeholder="请输入内容"
                v-model="contain">
            </el-input>
        </div>
        <!-- 加载页面 -->
        <Loading :dialogVisible="verDialogVisible"></Loading>
    </div>
</template>
<script>
import Loading from './../components/loading'
import API from '@/api/api'
export default {
  components: {
    Loading
  },
  data () {
    return {
      dialogImageUrl: '',
      dialogVisible: false,
      disabled: false,
      nameinput: '', // 名称
      typeinput: '', // 类型
      type: [], // 类型
      selectType: '', // 选中的类型
      contain: '', // 内容
      name: '', // 用户名称
      personId: '', // 用户id
      demandList: [], // 提报集合
      isshowerror: false, // 是否提示
      mess: '', // 错误提示
      id: '', // 提报id
      verDialogVisible: true // 加载页
    }
  },
  created () {
    this.id = this.$route.params.id
    this.getClassifyList()
  },
  mounted () {
    // 监听返回键
    document.addEventListener('backbutton', this.onBackKeyDown, false)
  },
  // 消除返回键监听
  beforeDestroy () {
    document.removeEventListener('backbutton', this.onBackKeyDown, false)
  },
  methods: {
    // 获取数据
    findDRById () {
      API.post('findDRById', {
        id: this.id
      }, 'json', (data, err) => {
        if (!data.success) {
          console.log(data.msg)
        } else if (data.success) {
          this.verDialogVisible = false
          this.nameinput = data.data.requireName
          this.typeinput = data.data.type
          this.contain = data.data.requireContent
        }
      })
    },
    // 需求类型
    getClassifyList () {
      API.post('getClassifyList', {
        dicId: this.GLOBAL.DEMAND.dicId
      }, 'json', (data, err) => {
        if (!data.success) {
          console.log(data.msg)
        } else if (data.success) {
          this.type = data.data
          this.findDRById()
        }
      })
    },
    // 返回
    back () {
      this.$router.back()
    },
    // 返回键
    onBackKeyDown () {
      this.back()
    }
  },
  watch: {
    typeinput (val, oldval) {
      for (var j = 0; j < this.type.length; j++) {
        if (this.typeinput === this.type[j].code) {
          this.selectType = this.type[j].name
        }
      }
    }
  }
}
</script>
<style scoped>
    .demand_look{
        width:100%;
        height:100vh;
    }
    .header{
        width:100%;
        background:#fff;
        font-size:0.38rem;
        font-family: PingFang-SC-Medium;
        position: fixed;
        top:0;
        height: 0.94rem;
        z-index: 99;
        text-align: center;
        border-bottom: 0.012rem solid  rgba(222,230,242,1);
    }
    .back{
        width:0.89rem;
        height:0.94rem;
        position: fixed;
        top:0;
        left:0;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 100;
    }
    .back img{
        width:0.19rem;
        height:0.34rem;
    }
    .demand_look .save{
        position: absolute;
        display: flex;
        width:1.01rem;
        height: 0.94rem;
        right:0;
        top:0;
        justify-content: center;
        align-items: center;
        font-size:0.32rem;
        color:#C2CBD6;
        opacity : 0.7;
    }
    .demandHead{
        line-height: 0.94rem;
        font-size:0.38rem;
        color:#4E4E4E;
    }
    .demandContainer{
        /* margin-top:0.94rem; */
        position: fixed;
        top:0.94rem;
        left:0;
        background: rgba(255,255,255,1);
        width:100%;
        /* height:calc(100vh - 0.94rem); */
        /* overflow-y: scroll;
        -webkit-overflow-scrolling: touch; */
    }
    .demandContainer .demandName{
        width:100%;
        padding:0 0.35rem;
        border-bottom: 0.012rem solid #DEE6F2;
        height:1.06rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .demandName .name{
        font-size:0.34rem;
        color:#6E747E;
    }
    .demandContainer .demandType{
        width:100%;
        padding:0 0.35rem;
        height:1.06rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .demandType .type{
        font-size:0.34rem;
        color:#6E747E;
    }
    .middle{
        width:100%;
        height:0.65rem;
        background:#F5F6F8;
    }
    .demandContain{
        position: fixed;
        bottom:0;
        /* margin-top:4.06rem; */
        /* height:calc(100vh - 4.06rem); */
        width:100%;
        /* padding-top:0.35rem; */
        background: #fff;
        /* overflow-y: scroll;
        -webkit-overflow-scrolling: touch; */
    }
</style>
<style>
    .demand_look .demandName .el-input{
        width:calc(100% - 1.2rem) !important;
    }
    .demand_look .demandName .el-input .el-input__inner{
        border:0;
        /* color:#C2CBD6; */
        font-size:0.34rem;
    }
    .demand_look .demandType .el-select{
        width:calc(100% - 1.2rem) !important;
    }
    .demand_look .demandType .el-select .el-input .el-input__inner{
        border:0;
        /* color:#C2CBD6; */
        font-size:0.34rem;
    }
    .demand_look .demandType .el-select .el-input .el-select__caret{
        color:#6E747E;
        font-size:0.34rem;
    }
    .demand_look .demandContain .el-textarea{
        max-height: calc(100vh - 4.06rem);
    }
    .demand_look .demandContain .el-textarea__inner{
        border:0;
        padding:0 0.35rem 0.35rem 0.35rem;
        max-height: calc(100vh - 4.06rem);
        font-size:0.32rem;
        min-height: calc(100vh - 4.06rem) !important;
        word-break: break-word;
    }
    .demand_look .demandName .el-input.is-disabled .el-input__inner{
        background: #fff;
    }
    .demand_look .el-textarea.is-disabled .el-textarea__inner{
        background: #fff;
    }
    .demand_look .demandType .el-input.is-disabled .el-input__inner{
        background: #fff;
    }
</style>



